* {
  box-sizing: border-box;
}
.searchItems {
  background-color: black;
  color: white;
  height: 100vh;
  width: 100%;
}
.searchbar {
  border: 2px solid grey;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  width: fit-content;
}
.input {
  margin-right: 2%;
}
